body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.cursor-pointer {
  cursor: pointer;
}
  

.bg {
  width: auto;
  height: 500px;
  background-image: url(../public/iu1.jpg);
  background-size: cover;
  background-position: center 70%;
}
.bg1 {
  width: auto;
  height: 500px;
  background-image: url(../public/iu3.jpg);
  background-size: cover;
  background-position: center center;
}

.bg2 {
  width: auto;
  height: 500px;
  background-image: url(../public/iu4.jpg);
  background-size: cover;
  background-position: center center;
}

.title{
  width: 500px;
  position: absolute;
  bottom: 50px;
  z-index: 1;
  font-size: 25px;
  padding: 10px 0px;
  background-color: rgba(255,255,255,0.8);
}

.title_cover{
  position: relative;
}

.bottom{
  width: 100%;
  height: 50px;
  margin-top: 20px;
  color:#fff;
  text-align: center;
  line-height: 50px;
  background-color: #000;
}

.page-link{
  color: #353535;
}

.page-link:hover{
  color: #353535;
}

.active>.page-link, .active>.page-link:hover{
  background-color: #353535;
  border-color: #353535;
  color: #fff;

}